import deepEqual from 'fast-deep-equal';
import { useEffect, useRef, useState } from 'react';
import { EventEmitter } from 'events';

import { checkRestrictions } from '@mssgme/shared';
import { useWorkspace } from './useWorkspace';
import { useEventCallback } from './useEventCallback';

// TODO: refactor, use redux, context or useSyncExternalStore instead of EventEmitter
export function createHooks() {
    const ee = new EventEmitter();

    ee.setMaxListeners(100);

    const show = (...args) => ee.emit('show', ...args);
    const hide = () => ee.emit('hide');

    function useWarning() {
        const [state, setState] = useState({ isPresented: false, warning: null });

        useEffect(() => {
            const onShow = (warning) => setState({ isPresented: true, warning });
            const onHide = () => setState((prevState) => ({ ...prevState, isPresented: false }));

            ee.on('show', onShow);
            ee.on('hide', onHide);

            return () => {
                ee.removeListener('show', onShow);
                ee.removeListener('hide', onHide);
            };
        }, []);

        return {
            ...state,
            show,
            hide,
        };
    }

    function useRestriction(rule, subject) {
        const workspace = useWorkspace();
        const ref = useRef(null);
        const [warning, setWarning] = useState(null);

        const checkRule = useEventCallback(
            (rule, subject) => checkRestrictions({ workspace }, rule, subject, setWarning)
        );
        const showWarning = useEventCallback((w) => show(w || warning));
        const clearWarning = useEventCallback(() => setWarning(null));
        const handleBlockedEvent = useEventCallback((e) => {
            if (e?.preventDefault) {
                e.preventDefault();
                e.stopPropagation();
            }

            show(warning);
        });

        if (!ref.current) {
            ref.current = [rule, subject];
        } else if (subject !== ref.current[1]) {
            const isObject = subject && typeof subject === 'object';

            if (!(isObject && deepEqual(subject, ref.current[1]))) {
                ref.current = [rule, subject];
            }
        }

        useEffect(() => {
            if (rule) {
                checkRule(rule, subject);
            }
        }, ref.current);

        return {
            warning,
            hasWarning: !!warning,
            showWarning,
            clearWarning,
            hideWarning: hide,
            handleBlockedEvent: warning ? handleBlockedEvent : undefined,
            checkRule,
        };
    }

    return { useWarning, useRestriction };
}

const { useWarning, useRestriction } = createHooks();

export { useWarning, useRestriction };
