export const isPaid = ({ plan, status, paid }, allowPastDue = false) => {
    if (plan && (status === 'trialing' || plan === 'basic' || plan === 'unlimited')) {
        return true;
    }

    return paid || (allowPastDue && status === 'past_due');
};

export const isBasic = (billing, allowPastDue = false) => billing.plan === 'basic' || !isPaid(billing, allowPastDue);
